@charset "utf-8";
.flex {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
.l_flex {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
}
body {
  font-size: 14px;
  color: #6c849c;
  background: #fff;
}
html,
body,
div,
table,
ul,
li,
ol,
dl,
dd,
dt,
a,
img,
label,
p,
h1,
h2,
h3,
h4,
h5,
h6,
span {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}
input {
  margin: 0;
  padding: 0;
}
ul,
ol {
  list-style: none;
}
em,
i {
  font-style: normal;
}
strong {
  font-weight: normal;
}
img {
  max-width: 100%;
  display: block;
}
textarea {
  resize: none;
}
form {
  margin: 0;
}
button {
  outline: none!important;
}
button:focus {
  outline: none!important;
}
a {
  color: #6c849c;
}
a,
a:hover,
a:focus {
  text-decoration: none;
  outline: none;
}
a:hover,
a:focus {
  color: #03acef;
}
.wrap {
  width: 100%;
  margin: 0 auto;
}
/* 公共 start */
.iconfont {
  font-size: 20px;
  vertical-align: middle;
}
/* 公共 end */
/* 头部start */
.header {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  padding: 0 18px;
  align-items: center;
  width: 100%;
  height: 60px;
  background: #009bd6 url("../images/top_bg.png") no-repeat;
  /* position: fixed;
    left: 0;
    top: 0;
    z-index: 2;*/
  box-shadow: 1px 1.732px 5px rgba(133, 133, 133, 0.32);
}
.header a {
  color: #fff;
  letter-spacing: 1px;
}
.header .logo {
  width: 276px;
}
.header .nav-list {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
  height: 100%;
  padding-left: 10%;
}
.header .nav-list ul {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  flex-wrap: wrap;
  height: 100%;
}
.header .nav-list ul li {
  margin: 0 12px;
}
.header .nav-list ul li a {
  padding: 0 8px;
  display: block;
  height: 100%;
  line-height: 60px;
}
.header .nav-list ul li.active a,
.header .nav-list ul li:hover a {
  background: linear-gradient(#028ec3, #0798d2);
}
.header .nav-list ul li.active:after,
.header .nav-list ul li:hover:after {
  content: '';
  margin-top: -3px;
  width: 100%;
  display: block;
  height: 3px;
  border-radius: 1px;
  background-color: #0eb6f8;
}
.header .set {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  width: 115px;
}
.header .set .set-login {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
}
.header .set .set-login:after {
  content: '';
  width: 1px;
  height: 12px;
  background-color: #ffffff;
  display: block;
  margin: 0 9px;
}
/* 头部end */
/* 左侧 start */
.content {
  width: 100%;
  min-width: 1280px;
  padding: 20px;
  height: calc(40vh);
}
.content .left-sidebar {
  width: 200px;
  height: calc(100vh - 115px);
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 0 16px rgba(185, 206, 223, 0.32);
}
.content .left-sidebar a {
  width: 100%;
  display: block;
}
.content .left-sidebar ul li {
  border-bottom: 1px solid #d4e1e9;
}
.content .left-sidebar ul li > a {
  height: 44px;
  line-height: 44px;
  padding: 0 5px;
}
.content .left-sidebar ul li > a em {
  float: right;
  display: flex;
  align-items: center;
  height: 44px;
  margin-right: 3px;
}
.content .left-sidebar ul li > a i {
  vertical-align: -2px;
  margin-right: 6px;
}
.content .left-sidebar ul li.active > a,
.content .left-sidebar ul li:hover > a {
  background-color: #03acef;
  color: white;
}
.content .left-sidebar ul li .left-side-kid {
  padding: 0 6px;
}
.content .left-sidebar ul li .left-side-kid a {
  margin: 5px 0;
  padding: 0 10px;
  height: 34px;
  line-height: 34px;
  display: flex;
  align-items: center;
  border: 1px dotted transparent;
}
.content .left-sidebar ul li .left-side-kid a:before {
  content: '';
  display: block;
  width: 6px;
  height: 6px;
  margin-right: 7px;
  border-radius: 3px;
  background-color: #8fa1b5;
}
.content .left-sidebar ul li .left-side-kid a.active,
.content .left-sidebar ul li .left-side-kid a:hover {
  color: #6c849c;
  border-radius: 3px;
  background-color: #d4e1e9;
  border: 1px dotted #91a3b6;
}
.content .left-sidebar ul li .left-side-kid a.active:before,
.content .left-sidebar ul li .left-side-kid a:hover:before {
  background-color: #fdd835;
}
/* 左侧 end */
